<template>
        <form id="form-area-container" class="message-form-container" @submit.prevent="handleSubmit" ref="container">
            <div class="form-item">
                <div class="input-area">
                    <input type="text" placeholder="用户昵称" maxlength="10" v-model="formData.inputData">
                    <span class="number">{{formData.inputData.length}}/10</span>
                </div>
                <div class="error">{{err.inputErr}}</div>
            </div>
            <div class="form-item">
                <div class="text-area">
                    <textarea placeholder="输入内容" maxlength="300" v-model="formData.textData"></textarea>
                <span class="number">{{formData.textData.length}}/300</span>
                </div>
                <div class="error">{{err.textErr}}</div>
            </div>
            <div class="form-item">
                <div class="button-area">
                    <button :class="{disable:isSubmitting}">{{isSubmitting ? '提交中...':'提交'}}</button>
                </div>
            </div>
        </form>
</template>

<style lang="less" scoped>

    .message-form-container{
        
        margin-bottom:20px;
        .form-item{
            button{
                    position: relative;
                    cursor: pointer;
                    border: none;
                    outline: none;
                    width: 100px;
                    height: 34px;
                    color: #fff;
                    border-radius: 4px;
                    background: #6b9eee;
                    &.disable{
                        cursor:not-allowed;
                        background: #b4b8bc;
                    }
            }
            .error{
                        margin-top: 6px;
                        color: #cc3600;
                        font-size: 14px;
                        height: 20px;
                        line-height: 20px;
                }
            margin-bottom:8px;
            .input-area{
                width:50%;
                position:relative;
                input{
                    padding: 0 15px;
                    height: 40px;
                }
            }
            .text-area{
                position:relative;
                textarea{
                    resize: none;
                    padding: 8px 15px;
                    height: 120px;
                }
            }
            .text-area,.input-area{
                .number{
                    position:absolute;
                    bottom:5px;
                    right:5px;
                        color: #b4b8bc;
                        font-size: 12px;
                }
                input,textarea{
                    display: block;
                    width: 100%;
                    box-sizing: border-box;
                    border: 1px dashed #b4b8bc;
                    outline: none;
                    color: #373737;
                    font-size: 14px;
                    border-radius: 4px
                }
                
            }
        }
    }
</style>
<script>
export default {
    data(){
        return{
            formData:{
                inputData:'',
                textData:'',
                
            },
            err:{
                inputErr:'',
                textErr:''
            },
            isSubmitting:false,
            
        }
    },
    methods:{
        handleSubmit(){
            if(!this.formData.inputData){
                this.err.inputErr = '请输入昵称';
                
            }else{
                this.err.inputErr = '';
            }
            if(!this.formData.textData){
                this.err.textErr = '请输入评论内容';
                
            }else{
                this.err.textErr = '';
            }

            if(!this.formData.inputData || !this.formData.textData){
                return;
            }

            this.isSubmitting = true;
            this.$emit('submit',this.formData,(result)=>{
                this.$showMessage({content:result,type:'success', duration:2000,container:this.$refs.container, callback:()=>{
                    this.isSubmitting = false;
                    this.formData.inputData = '';
                    this.formData.textData = '';
                }})
               
            });
        }
    }
};
</script>

