<template>
    <div class="blog-list-container" ref="container" v-loading="isLoading">
       <ul>
           <li v-for="item in data.rows" :key="item.id">
               <div class="thumb" v-if="item.thumb">
                   <router-link :to="{name:'BlogDetail',params:{id:item.id}}">
                       <img v-lay="item.thumb" :alt="item.title">
                   </router-link>
               </div>
               <div class="main">
                   <router-link :to="{name:'BlogDetail',params:{id:item.id}}"><h2>{{item.title}}</h2></router-link>
                   <div class="aside">
                       <span>日期：{{formatDate(+item.createDate)}}</span>
                       <span>浏览：{{item.scanNumber}}</span>
                       <span>评论：{{item.commentNumber}}</span>
                       <router-link :to="{
                           name:'BlogCategory',
                           params:{
                               categoryId:item.category.id
                           }
                           }"> {{item.category.name}} </router-link>
                   </div>
                   <div class="des">
                        {{item.description}}
                   </div>
               </div>
           </li>
       </ul>
        <pager  @changePage="handlePage" :current="+routeInfo.page" :limit="+routeInfo.limit" :total="+getTotal"></pager>
    </div>
</template>

<style lang="less" scoped>
@import '~@/style/var.less';
h2{
    margin:0px;
}
.blog-list-container{
    scroll-behavior:smooth;
    padding:20px;
    position:relative;
    overflow-y:scroll;
    width:100%;
    height:100%;
    box-sizing: border-box;
    line-height: 1.7;
    li{
        display:flex;
        padding:15px 0;
        border-bottom: 1px solid @gray;
        .thumb{
            flex:0 0 auto;
            margin-right:15px;
            img{
                display: block;
                height: 170px;
                width:170px;
                border-radius: 5px;
            }
        }
        .main{
            flex:1 1 auto;
            .aside{
                font-size:12px;
                color:@gray;
                span{
                    margin-right: 15px;
                }
            }
            .des{
                margin:15px 0;
                font-size:14px;
            }
        }
    }
}
</style>

<script>
import fetchData from '@/mixins/fetchData.js'
import {getBlogs} from '@/api/blog.js'
import loading from '@/directives/loading.js'
import Pager from '@/components/Pager'
import {formatDate} from '@/utils/formatDate.js'
import {mainScroll} from '@/mixins/mainScroll.js'
export default {
    watch:{
        $route:async function(){
            // 重新获取数据
            this.isLoading = true;
            this.$refs.container.scrollTop = 0;
            this.data = await this.fetchData();
            this.isLoading = false;
        }
    },
    components:{
        'pager':Pager,
    },
    directives:{
        loading,
    },
    mixins:[fetchData({}),mainScroll('container')],
    computed:{
        routeInfo(){
            const categoryId = this.$route.params.categoryId || -1;
            const page = this.$route.query.page || 1;
            const limit = this.$route.query.limit || 20;
            return {
                categoryId,
                page,
                limit,
            }
        },
        getTotal(){
            return this.data.total;
        }
    },
            
        
        
       
    methods:{
        formatDate,
        async fetchData(){

            const data = await getBlogs(this.routeInfo.page,this.routeInfo.limit,this.routeInfo.categoryId);
            
            return data;
        },
        handlePage(item){
            // 改变url 
            // 两种，一种是有分类的，一种是无分类的
            // （1）/blog/cate/1?page=item&limit=1
            // (2)/blog?page=item&limit=1
            const query={
                        page:+item,
                        limit:+this.$route.query.limit || 20,
                    };
            if(this.$route.params.categoryId == -1){
                this.$router.push({
                    name:'BlogCategory',
                    query,
                    params:{
                        categoryId:this.$route.params.categoryId
                    }
                })}else{
                this.$router.push({
                    name:'Blog',
                   query,
            }
                )
            
            }
        
    },
           

}
};
</script>
