<template>
    <div class="imageloader-container">
        <!-- <img class="place" v-if="!everyDone" :src="placeholder" alt=""> -->
        <div class="background-content-image" :style="{backgroundImage:`url(${placeholder})`}"></div>
        <img :src="src" 
        @load="loadFinish" 
        :style="{
            transition:`all ${duration/1000}s ease-in-out`,
            opacity:isActive? 1:0,
            }">
    </div>
</template>
<script>
export default {
    data(){
        return{
            isActive:false,
            everyDone:false,
        }
    },
    props:{
        src:{
            type:String,
            required:true,
        },
        placeholder:{
            type:String,
            required:true,
        },
        duration:{
            type:Number,
            default:500,
        }
    },
    methods:{
        loadFinish(){
            this.isActive = true;
           setTimeout(()=>{
               this.everyDone = true;
                this.$emit('load');
           },this.duration)
           
        }
    }
}
</script>
<style lang="less" scoped>
@import '~@/style/mix.less';
.imageloader-container{
        width:100%;
        height:100%;
        position:relative;
        overflow: hidden;
    .background-content-image{
        .self-fill();
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        filter: blur(1vw);
        z-index:-1;
    }
    img{
        position:absolute;
        left:50%;
        top:0%;
        border-radius:5px;
        height:100%;
        transform: translateX(-50%);
        // object-fit: none;
    }
    // .place{
    //     filter: blur(2vw);
    // }
    
}
</style>