<template>
    <div class="blog-toc-container">
            <right-list :list="widthSelected" @itemClick="handleClick"></right-list>
    </div>
</template>
<style lang="less" scoped>
.blog-toc-container{
    width:300px;
    height:100%;
    position:relative;
    overflow-y: auto;
    padding:20px;
    box-sizing: border-box;
}
</style>
<script>
import RightList from '../RightList'
import eventBus from '@/eventBus.js'
import bounce from '@/utils/bounce.js'
export default {
    data(){
        return {
            activeAnchor:'',
        }
    },
    components:{
        RightList,
    },
    props:{
         // [{name,isSelected,children:[{},{}]},{}]
        list:{
            type:Array,
        }
    },
    methods:{
        handleClick(item){
            location.hash = item.anchor;
        },

        setSelected(){
            this.activeAnchor = '';
            // 遍历所有dom标题元素，判断元素到浏览器的高度
            const range = 200;
            for(const d of this.getDoms){
                const rect = d.getBoundingClientRect();
                if(rect.top >=0 && rect.top < range){
                   
                    // 在range范围内
                    this.activeAnchor = d.id;
                    return;
                }else if(rect.top > range){
                    return;
                    // 在range范围外，在上面
                }else if(rect.top < 0){
                    // 在range下面  
                    this.activeAnchor = d.id;

                }
            }
        },
        

    },
    mounted() {
            const setSelectedBounce = bounce(this.setSelected,50)
            eventBus.$on('mainScroll',setSelectedBounce);
    },   
    destroyed() {
        eventBus.$off('mainScroll',this.setSelected);
    },  

    computed:{
        widthSelected(){
            const getToc = (toc = [])=>{
                return toc.map((item)=>({
                        ...item,
                        isSelected:item.anchor===this.activeAnchor,
                        children:getToc(item.children)
                    })
                )
            }
            return getToc(this.list);
        },
        getDoms(){
            const dom = [];
            // console.log(list)
            const addToDoms = (list)=>{
                for(const t of list){
                const d = document.getElementById(t.anchor)
                dom.push(d);
                if(t.children && t.children.length > 0){
                    addToDoms(t.children);
                }
            }
            }
            addToDoms(this.list);
            return dom;
        }
        
    },

};
</script>

