<template>
    <ul class="contact-container">
        <li>
            <a>
                <div class="icon"><icon-icon type="github"></icon-icon></div>
                <span>{{data.githubName}}</span>
            </a>
        </li>
        <li>
            <a>
                <div class="icon"><icon-icon type="mail"></icon-icon></div>
                <span>{{data.mail}}</span>
            </a>
        </li>
        <li>
            <a :href="`tencent://message/?Menu=yes&uin=${data.qq}&Service=300&sigT=45a1e5847943b64c6ff3990f8a9e644d2b31356cb0b4ac6b24663a3c8dd0f8aa12a595b1714f9d45`">
               <div class="icon"> <icon-icon type="qq"></icon-icon></div>
                <span>{{data.qq}}</span>
                
            </a>
            <div class="box box-qq"><img :src="data.qqQrCode" alt=""> 
            </div>
        </li>
        
        <li>
            <a :href="`mailto:${data.weixin}`">
                <div class="icon weixin"><icon-icon type="weixin"></icon-icon></div>
                <span>{{data.weixin}}</span>
            </a>
            <div class="box box-weixin"><img :src="data.weixinQrCode" alt=""></div>
                
        </li>
    </ul>
    
</template>
<script>
import '@/style/global.less'
import Icon from '@/components/Icon'
import {mapState} from 'vuex'
export default {
    components:{
        "icon-icon":Icon,
    },
    computed:{
        ...mapState('setting',['data'])
    }
}
</script>
<style lang="less" scoped>
@import '~@/style/var.less';
@import '~@/style/mix.less';
@itemHeight:30px;
// 之后的全局样式
.contact-container{
    // margin-top:100px;
    padding:20px;
    // width:100%;
    // box-sizing: border-box;
    color:@gray;
    li{
        height:@itemHeight;
        line-height:@itemHeight;
        margin:14px 0;
        position: relative;
        a{
        cursor: pointer;
        align-items: center;
        display:flex;
        span{
            font-size:14px;
        }
        
    }
    &:hover{
            .box{
                    transform: scaleY(1);
            }
           
        }
    .box{
        padding:10px 15px;
        position:absolute;
        background-color:#fff;
        border-radius:5px;
        left:0px;
        bottom:@itemHeight;
        transform: scaleY(0);
        z-index:3;
        transition: .3s;
        transform-origin:center bottom;
        img{
            width:150px;
            height:150px;
        }
        &::after{
            content:'';
            position: absolute;
            left:50%;
            transform: translateX(-50%) rotate(45deg);
            bottom:-4px;
            width:8px;
            height:8px;
            background-color:#fff;

        }
    }
    }
    .icon{
        font-size:26px;
        width:36px;
        &.weixin{
            font-size:32px;
            text-indent: -3px;
        }
    }
   
}
</style>