<template>
    <div class="detail-blog-container">
        <layout>
            <div ref="container" class="main-container"  v-loading="isLoading">
                <blog-detail :list="data"></blog-detail>
                <blog-comment v-if="!isLoading"></blog-comment>
            </div>
            <template #right>
                <div class="right-container">
                    <blog-toc :list="data.toc"  v-loading="isLoading"></blog-toc>
                </div>
            </template>
        </layout>
    </div>
</template>

<script>
import loading from '@/directives/loading.js'
import fetchData from '@/mixins/fetchData.js'
import {getBlog} from '@/api/blog.js'
import Layout from '@/components/Layout'
import BlogDetail from './components/BlogDetail'
import BlogToc from './components/BlogToc'
import BlogComment from './components/BlogComment'
import {mainScroll} from '@/mixins/mainScroll.js'
import setTitle from '@/utils/setTitle.js'
export default {
    components:{
        'layout':Layout,
        BlogDetail,
        BlogToc,
        BlogComment,
    },
    mixins:[fetchData({}),mainScroll('container')],
        directives:{
            loading,
        },
        methods:{
            async fetchData(){
                const data =  await getBlog(this.$route.params.id);
                this.setHash();
                setTitle.setRouterTitle(data.title)
                return data;
            },
           
            setHash(){
                const hash = location.hash;
                location.hash = '';
                setTimeout(()=>{
                    location.hash = hash;
                },500)
            }
        },
       
       
};
</script>

<style lang="less" scoped>
.detail-blog-container,.main-container,.right-container{
    width:100%;
    height:100%;
    position:relative;
}
.main-container{
     overflow-y: scroll;
        box-sizing: border-box;
         scroll-behavior: smooth;
        padding:20px;
}
</style>