<template>
    <div class="about-container" v-loading="loading || isSiteLoading">
            <iframe v-if="data" :src="data" frameborder="0" @load="handleLoad"></iframe>
    </div>
</template>
<script>
import loading from '@/directives/loading.js'
import {mapState} from 'vuex'
export default {
     data(){
         return {
             isSiteLoading:true,
         }
     },
    directives:{
        loading,
    }, 
    methods:{
        handleLoad(){
            this.isSiteLoading = false;
        }
    },
    computed:{
        ...mapState('about',['loading','data'])
    },
    created() {
        this.$store.dispatch('about/fetchAbout');
    },
}
</script>

<style lang="less" scoped>
.about-container{
    width:100%;
    height:100%;
    overflow:hidden;
    position:relative;
    iframe{
        width:100%;
        height:100%;
    }
}
</style>