<template>
    <div class="blog-comment-container">
            <message-area title="评论列表" 
            :subTitle="data.total"
            :list="data.rows"
            :isListLoading="isLoading"
            @submit="handleSubmit"></message-area>
    </div>
</template>

<script>
import MessageArea from '@/components/MessageArea'
import fetchData from '@/mixins/fetchData.js'
import {getComments} from '@/api/blog.js'
import {postComment} from '@/api/blog.js'
export default {
    mixins:[fetchData({})],
    components:{
        MessageArea,
    },
    data(){
        return {
            page:1,
            limit:10,
            total : 0,
        }
    },
    methods:{
        async fetchData(){
            return await getComments(this.page,this.limit,this.$route.params.id);
        },

        async handleSubmit(data,callback){
            const result = await postComment({...data,blogId:this.$route.params.id});
            this.data.rows.unshift(result);
            this.data.total ++;
            callback('评论成功');
        },
        // 相当于分页获取数据  只是将新获取的数据加入到之前上一页下面
        async getMore(container){
            const range = 50;
            if(container.scrollHeight - (container.scrollTop + container.clientHeight) > range || this.isLoading == true){
                return;
            }
            if(this.isMoreTotal){
                return;
            }
            this.page ++;
            this.isLoading = true;
            const resp = await getComments(this.page,this.limit,this.$route.params.id);
            this.isLoading = false;
            this.data.rows = [...this.data.rows,...resp.rows];
            this.total  = this.data.rows.length;
        }
    },
    created() {
        this.$eventBus.$on('mainScroll',this.getMore);
    },
    destroyed() {
        this.$eventBus.$off('mainScroll',this.getMore)
    },
    computed:{
        isMoreTotal(){
            if(this.total > this.data.total){
                return true;
            }else{
                return false;
            }
        }
    }
};
</script>

<style lang="less" scoped>

</style>