import Vue from 'vue'
/**
 * 获取某个组件渲染后的根元素
 * @param {*} comp  组件名
 * @param {*} props  属性
 */
export default function getComponentRootDom(comp, props) {
    const vm = new Vue({
            render: (h) => {
                return h(comp, { props })
            }
        })
        // console.log(comp)
    vm.$mount();
    return vm.$el;
}