<template>
    <div class="to-top-container" v-show="isShow" @click="handleClick">
            top
    </div>
</template>

<script>
export default {
    data(){
        return {
            isShow:false,
        }
    },
    methods:{
        handleShow(container){
            if(!container){
                this.isShow = false;
                return;
            }
            const range = 800;
            if(container.scrollTop > range){
                this.isShow = true;
            }else{
                this.isShow = false;
            }
        },
        handleClick(){
            this.$eventBus.$emit('setMoveTo',0);
        }
    },
    created() {
        this.$eventBus.$on('mainScroll',this.handleShow);
    },
    beforeDestroy() {
        this.$eventBus.$off('mainScroll',this.handleShow)
    },
};
</script>

<style lang="less" scoped>
.to-top-container{
    width:50px;
    height:50px;
    position:fixed;
    bottom:50px;
    right:50px;
    color:#fff;
    background-color:#6b9eee;
    border-radius:50%;
    text-align: center;
    line-height:50px;
    cursor: pointer;
}
</style>