import eventBus from "../eventBus"
import bounce from '../utils/bounce.js'
import defaultUrl from '../assets/default.gif'
let imgs = [];
function handleImage(img){
    img.dom.src=defaultUrl;
    const height = 170;
    // 在可视区范围内  加载图片，替换默认
    const rect = img.dom.getBoundingClientRect();
    const clientHight = document.body.clientHeight
    
    if(rect.top > -height && rect.top < clientHight){
        // 位于可视区
        // 替换src 
        setTimeout(function(){
            img.dom.src = img.src;
        },Math.ceil(Math.random() * 1000 + 500))

        imgs = imgs.filter((i)=>{return i.dom !== img.dom})
    }
    
    
}
function handleImageScroll(){
    // 图片的处理、
    // 遍历所有图片元素，处理每一张图片

    for(const img of imgs){
        handleImage(img);
    }
}
// 防抖
const handleImageScrollBounce = bounce(handleImageScroll)

// 向事件总线注册监听函数
eventBus.$on('mainScroll',handleImageScrollBounce)
export default {
    inserted(el,binding) {
        // console.log(el,binding)
        const currentImage = {
            dom:el,
            src:binding.value,
        }
        imgs.push(currentImage)
        handleImage(currentImage);
        
        
    },

    unbind (el) {
        imgs = imgs.filter((i)=>el !== i.dom);
        // eventBus.$off('mainScroll',handleImageScrollBounce)
    },
    
    
}