<template>

    <i class="iconfont icon-container" :class="{[className]:isActive}"></i>
</template>
<script>
const classMap = {
  home: "iconzhuye",  
  success: "iconzhengque",
  error: "iconcuowu",
  close: "iconguanbi",
  warn: "iconjinggao",
  info: "iconxinxi",
  blog: "iconblog",
  code: "iconcode",
  about: "iconset_about_hov",
  weixin: "iconweixin",
  mail: "iconemail",
  github: "icongithub",
  qq: "iconsign_qq",
  arrowUp: "iconiconfonticonfonti2copy",
  arrowDown: "iconiconfonticonfonti2",
  empty: "iconempty",
  chat: "iconliuyan",
};

export default {
    data(){
        return{
            isActive:true,
        }
    },
    props:{
        type:{
            type:String,
            required:true,
        }
    },
    // 计算属性  当它的依赖属性classMap不改变时，每次不重新计算，有缓存
    computed:{
        className(){
            return classMap[this.type];
        }
    }
}
</script>
<style scoped>
@import url('//at.alicdn.com/t/font_2164449_nalfgtq7il.css');
.icon-container{
    font-size:inherit;
}
</style>