<template>
    <div class="blog-category-container"  v-loading="isLoading">
        <h2>文章分类</h2>
        <right-list :list="list" @itemClick="handleClick"></right-list>
    </div>
</template>
<style lang="less" scoped>
.blog-category-container{
    width:300px;
    height:100%;
    position: relative;
    padding:20px;
    box-sizing: border-box;
    overflow-y:auto;
    h2{
        font-size:1em;
        font-weight:blod;
        letter-spacing: 2px;
        margin:0px;
    }

}
</style>
<script>
import RightList from '../RightList'
import fetchData from '@/mixins/fetchData.js'
import {getBlogTypes} from '@/api/blog.js'
import loading from '@/directives/loading.js'
export default {
     directives:{
        loading,
    },
    mixins:[fetchData([])],
    components:{
        RightList,
    },
    methods: {
        async fetchData(){
            return await getBlogTypes();
        },
        handleClick(item){
            // 变化url 
            const categoryId = item.id;
            const query={
                page:1,
                limit:this.$route.query.limit || 10,
            }
            // 也是分两种
           
               if(categoryId == -1){
                    this.$router.push({
                    name:'Blog',
                    query,
            })
               }else{
                   this.$router.push({
                    name:'BlogCategory',
                    query,
                    params:{
                        categoryId,
                    }
            })
               }
            
        }
    },
    computed:{
        getCategoryId(){
            return this.$route.params.categoryId || -1;
        },
        list(){
            
            const totalNumber = this.data.reduce((a,b)=>{return a+b.articleCount},0);
            const totalType = {name:'全部分类',articleCount:totalNumber,id:-1};
            const list = [totalType,...this.data];
            return list.map((item)=>{
                return {...item,isSelected : item.id == this.getCategoryId};
            })

        }
    }
};
</script>

