<template>
    <div class="carousel-container" ref="outcontainer" @mousemove="handleMouseMove" @mouseleave="handleMouseLeave">
        <div class="imageLoader-container" ref="incontainer"  :style="getPosition">
            <image-loader @load="handleLoad" :src="carousel.bigImg" :placeholder="carousel.midImg"></image-loader>
        </div>
        <div class="title" ref="title" >{{carousel.title}}</div>
        <div class="des" ref="des" >{{carousel.description}}</div>
    </div>
</template>
<style lang="less" scoped>
@import '~@/style/var.less';
    .carousel-container{
        width:100%;
        height:100%;
        // background-color: @dark;
        color:#fff;
        position:relative;
       overflow:hidden;
        .imageLoader-container{
            width:110%;
            height:110%;
            position:absolute;
            z-index:-1;
            left:0px;
            top:0px;
            // transition: 2s;
            transition: 0.3s;
            overflow:hidden;

        }
        .title,.des{
            position:absolute;
            left:25px;
            letter-spacing: 3px;
            text-shadow:1px 0px 0px rgba(0,0,0,.5),-1px 0px 0px rgba(0,0,0,.5),0px 1px 0px rgba(0,0,0,.5),0px -1px 0px rgba(0,0,0,.5);  
            white-space: nowrap;
            overflow: hidden;
            opacity: 0;
            
       }
        .title{
            top:calc(50% - 40px);
            font-size:2em; 
        }
        .des{
            top:calc(50% + 20px);
            font-size:1.2em;
        }
    }

</style>
<script>
import ImageLoader from '@/components/ImageLoader'
export default {
    props:['carousel'],
    components:{
            ImageLoader,
    },
    computed:{
        getPosition(){
            if(!this.outSize || !this.inSize){
                return;
            }
            const extraWidth = this.inSize.width - this.outSize.width;
            const extraHeight = this.inSize.height - this.outSize.height;
            const x = extraWidth / this.outSize.width * this.mouseX;
            const y = extraHeight / this.outSize.height * this.mouseY;
            return {
                // left:-x + 'px',
                // top:-y + 'px',
                transform:`translate(${-x}px,${-y}px)`,

            }
        }
    },
    data(){
        return{
            titleWidth:0,
            desWidth:0,
            outSize:null,
            inSize:null,
            mouseX:0,
            mouseY:0,
        }
    },
    mounted() {
        this.titleWidth = this.$refs.title.clientWidth;
        this.desWidth = this.$refs.des.clientWidth;
        this.getSize();
        window.addEventListener('resize',this.getSize)
    },
    destroyed() {
        window.removeEventListener('resize',this.getSize)
    },
    methods:{
        handleMouseLeave(){
            this.mouseX = this.outSize.width / 2;
            this.mouseY = this.outSize.height / 2
        },
        handleMouseMove(e){
            // 鼠标位于容器内的位置
            
            const rect = this.$refs.outcontainer.getBoundingClientRect();
            this.mouseX = e.clientX - rect.left;
            this.mouseY = e.clientY - rect.top;
            
        },

        getSize(){
            this.outSize = {
                width:this.$refs.outcontainer.clientWidth,
                height:this.$refs.outcontainer.clientHeight,
            },
            this.inSize = {
                width:this.$refs.incontainer.clientWidth,
                height:this.$refs.incontainer.clientHeight,
            }
            this.mouseX = this.outSize.width / 2;
            this.mouseY = this.outSize.height / 2
        },
        handleLoad(){
            this.showWords();
        },
        showWords(){
            this.$refs.title.style.width = 0 + 'px';
            this.$refs.title.style.opacity = 0;
            
            this.$refs.title.style.transition="1s";
            this.$refs.title.clientWidth;
            this.$refs.title.style.width = this.titleWidth + 'px';
            this.$refs.title.style.opacity = 1;


            this.$refs.des.style.width = 0 + 'px';
            this.$refs.des.style.opacity = 0;
             
            this.$refs.des.style.transition="1s 1s";
            this.$refs.des.clientWidth;
            this.$refs.des.style.width = this.desWidth + 'px';
            this.$refs.des.style.opacity = 1;

        }

    }
    

}
</script>
