export function mainScroll(refValue){
    return {
        mounted() {
            this.$refs[refValue].addEventListener('scroll',this.handleScroll);
            this.$eventBus.$on('setMoveTo',this.handleMove)
        },
        beforeDestroy() {
            this.$refs[refValue].removeEventListener('scroll',this.handleScroll);
            this.$eventBus.$off('setMoveTo',this.handleMove);
            this.$refs[refValue] = null;
            
        },
        destroyed() {
            this.handleScroll();
        },
        methods:{
            handleMove(height){
                this.$refs[refValue].scrollTop = height;
            },
            handleScroll(){
                this.$eventBus.$emit('mainScroll',this.$refs[refValue])
                
                    // eventBus.$emit('setMoveTo',this.$refs.container)
                
            },
        }
    }
}