import Home from '../views/Home'
import Blog from '../views/Blog'
import About from '../views/About'
import Project from '../views/Project'
import Chat from '../views/Chat'
import DetailBlog from '../views/Blog/DetailBlog.vue'

export default [
    { name: 'Home', path: '/', component: Home,meta:{
        title:'首页'
    }},
    { name: 'Blog', path: '/blog', component: Blog,meta:{
        title:'个人博客'
    }},
    // 动态路由   categoryId不知道，是动态变化的
    { name: 'BlogCategory', path: '/blog/cate/:categoryId', component: Blog },
    { name: 'About', path: '/about', component: About,meta:{
        title:'关于我'
    } },
    { name: 'BlogDetail', path: '/blog/:id', component: DetailBlog,meta:{
        title:'博客详情'
    } },
    { name: 'Project', path: '/project', component: Project,meta:{
        title:'项目'
    } },
    { name: 'Message', path: '/message', component: Chat,meta:{
        title:'留言板'
    } },

]