import axios from 'axios'
import showMessage from '../utils/showMessage.js'

const ins = axios.create();
ins.interceptors.response.use(function(config) {
    if (config.data.code != 0) {
        showMessage({ content: config.data.msg, type: 'error', duration: 1500 })
        return null;
    }
    return config.data.data;
})

export default ins;