<template>
   <div v-loading="load" class="home-container" @wheel="handleWheel"> 
       <ul @transitionend="handleTransitionend"  class="list-container" ref="container" :style="{marginTop:getMargin}">
           <li v-for="item in data" :key="item.id">
               <carousel-item :carousel="item"></carousel-item>
           </li>
       </ul>
       <!-- 上箭头 -->
       <div class="icon icon-up" v-show="index >= 1" @click="turnPage(--index)">
           <icon type="arrowUp"></icon>
       </div>
       <!-- 下箭头 -->
       <div class="icon icon-down" v-show="index < data.length-1" @click="turnPage(++ index)">
           <icon type="arrowDown"></icon>
       </div>
        <!-- 旁边原点 -->
       <ul class="indicator">
           <li @click="turnPage(i)" :class="{active:index === i}" v-for="(item,i) in data" :key="item.id"></li>
       </ul>
       <!-- <loading v-if="isLoading"></loading> -->
   </div>
</template>
<style lang="less" scoped>
@import '~@/style/var.less';
.home-container{
    width:100%;
    height:100%;
    position:relative;
    overflow:hidden;
    @top:25px;
    .icon{
        position:absolute;
        cursor: pointer;
        left:50%;
        transform: translateX(-50%);
        font-size:30px;
        color:@gray;
        &.icon-up{
            top:@top;
            animation: jump-up 2s infinite;
        }
        &.icon-down{
            bottom:@top;
             animation: jump-down 2s infinite;
        }
        
    }
    .list-container{
        width:100%;
        height:100%;
        transition: 500ms;
        li{
            width:100%;
            height:100%;
        }
    }
    .indicator{
        position:absolute;
        top:50%;
        transform: translateY(-50%);
        right:20px;
        li{
            width:7px;
            height:7px;
            border-radius:50%;
            background-color:@dark;
            border:1px solid #fff;
            box-sizing: border-box;
            margin-bottom:10px;
            cursor: pointer;
            &.active{
                background-color:#fff;
            }
        }
    }
}

// 测试代码
// .home-container{
//     width:300px;
//     height:200px;
//     margin:100px auto;
//     outline:2px solid #f40;
    
// }
@hei:5px;
@keyframes jump-up {
    0%{
        transform: translate(-50%,@hei);
    }
    50%{
        transform: translate(-50%,-@hei);
    }
    100%{
        transform: translate(-50%,@hei);
    }
}
@keyframes jump-down {
    0%{
        transform: translate(-50%,-@hei);
    }
    50%{
        transform: translate(-50%,@hei);
    }
    100%{
        transform: translate(-50%,-@hei);
    }
}
</style>
<script>
import Icon from '@/components/Icon'
import CarouselItem from './CarouselItem.vue'
import loading from '@/directives/loading.js'

import {mapState} from 'vuex'
// import Loading from '@/components/Loading'
export default {
   
    directives:{
        loading,
    },
   components:{
       'icon':Icon,
       CarouselItem,
    //    'loading':Loading,
   },
   methods:{
      
       turnPage:function(i){
           this.index = i;
       },
       getHeight(){
            this.containerHeight = this.$refs.container.clientHeight;
       },
       handleTransitionend(){
           this.isSwitching = false;
       },
       handleWheel(e){
           if(this.isSwitching){
               return;
           }
           if(e.deltaY > 5){
               if(this.index < this.data.length-1){
                   this.isSwitching = true;
                   this.index ++;
               }
               
           }
           if(e.deltaY < -5){
               if(this.index > 0){
                    this.isSwitching = true;
                   this.index --;
               }
           }
       }
   },
   data(){
       return {
           index:0,
           containerHeight:0,
           isSwitching:false,
          
       }
   },
   computed:{
       getMargin:function(){
           return -this.index * this.containerHeight + 'px';
       },
       ...mapState('banner',['load','data'])
   },
   
   created() {
       this.$store.dispatch('banner/fetchData');
   },
   mounted() {
      this.getHeight();
      window.addEventListener('resize',this.getHeight);
   },
   destroyed() {
       window.removeEventListener('resize',this.getHeight)
   },

}
</script>
