        <h3>{{title}}<span>({{subTitle}})</span></h3>
        <message-list :list="list"></message-list>
        <div class="loading" v-loading="isListLoading"></div>
    </div>
</template>

<script>
import MessageForm from './MessageForm'
import MessageList from './MessageList'
import loading from '@/directives/loading.js'
export default {
    // mixins:[fetchData({})],
    directives:{
        loading,
    },
    props:{
    title:{
        type:String,
    },
    subTitle:{
        type:Number,
    },
    list:{
        type:Array,
        default:()=>[],
    },
    isListLoading:{
        type:Boolean,
        default:false,
    }
    },
    components:{
        MessageForm,
        MessageList,
    },

    
};
