let site = '';
let title="";
function setTitle(){
        if(!site || !title){
            document.title = 'loading...'
        }else if(site && !title){
           document.title = site;
        }else if(!site && title){
            document.title = title;
        }else{
            document.title = `${title}-${site}`;
        }
}

export default {
    setRouterTitle(routerSite){
        title = routerSite;
        setTitle();
    },
    setSiteTitle(siteTitle){
        site = siteTitle;
        setTitle();
    }
}