<template>
    <div class="chat-container" ref="container">
        <message-area @submit="handleSubmit" title="留言板" :subTitle="data.total" :list="data.rows" :isListLoading="isLoading"></message-area>
    </div>
</template>
<script>
import MessageArea from '@/components/MessageArea'
import loading from '@/directives/loading'
import fetchData from '@/mixins/fetchData.js'
import {postMessage} from '@/api/message.js' 
import {getMessage} from '@/api/message.js' 
import {mainScroll} from '@/mixins/mainScroll.js'
export default {
    // mixins:[],
    data(){
        return{
            page:1,
            limit:10,
            total : 0,
        }
    },
    mixins:[fetchData({}),mainScroll('container')],
    directives:{
        loading,
    },
    components:{
        MessageArea,
    },
    methods:{
        async fetchData(){
            return await getMessage(this.page,this.limit);
        },
        async handleSubmit(form,callback){
            const resp = await postMessage(form);
            this.data.rows.unshift(resp);
            this.data.total ++;
            callback('留言成功');
        },
        // 相当于分页获取数据  只是将新获取的数据加入到之前上一页下面
        async getMore(container){
            const range = 50;
            if(container.scrollHeight - (container.scrollTop + container.clientHeight) > range || this.isLoading == true){
                return;
            }
            if(this.isMoreTotal){
                return;
            }
            this.page ++;
            this.isLoading = true;
            const resp = await getMessage(this.page,this.limit);
            this.isLoading = false;
            this.data.rows = [...this.data.rows,...resp.rows];
            this.total  = this.data.rows.length;
        },
    },
    
       
    created() {
        this.$eventBus.$on('mainScroll',this.getMore);
    },
    destroyed() {
        this.$eventBus.$off('mainScroll',this.getMore)
    },
    computed:{
        isMoreTotal(){
            if(this.total > this.data.total){
                return true;
            }else{
                return false;
            }
        }
    }
}
</script>
<style lang="less" scoped>
.chat-container{
    width:100%;
    height:100%;
    display:flex;
    justify-content: center;
    padding:20px;
    box-sizing: border-box;
    overflow:auto;
    scroll-behavior: smooth;

}
.message-area-container{
    width:700px;
    
}
</style>