<template>
    <div class="pager-container" v-if="totalPage > 1">
        <a @click="handleClick(1)" :class="{disabled:current === 1}">|&lt;&lt;</a>
        <a @click="handleClick(current-1)" :class="{disabled:current === 1}">&lt;&lt;</a>
        <a v-for="(i,index) in pageNums" :key="index" :class="{active: i === current}" @click="handleClick(i)">{{i}}</a>
        <a @click="handleClick(current + 1)" :class="{disabled:current === totalPage}">&gt;&gt;</a>
        <a @click="handleClick(totalPage)" :class="{disabled:current === totalPage}">&gt;&gt;|</a>
    </div>
</template>

<script>
export default {
    props:{
        current:{
            type:Number,
            default:1,
        },
        total:{
            type:Number,
            default:0,
        },
        limit:{
            type:Number,
            default:10,
        },
        visibleNumber:{
            type:Number,
            default:10,
        }
    },
    computed:{
        totalPage(){
            return Math.ceil(this.total/this.limit)
        },
        vMinPage(){
            let temp =  this.current-(this.visibleNumber/2);
            if(temp < 1){
                temp = 1;
            }
            return temp;
        },
        vMaxPage(){
            let temp = this.vMinPage + this.visibleNumber - 1;
            if(temp > this.totalPage){
                temp = this.totalPage;
            }
            return temp;
        },
        pageNums(){
            let nums = [];
            for(let i = this.vMinPage; i <= this.vMaxPage;i ++){
                nums.push(i);
            }
            return nums;
        }

    },
    methods:{
        handleClick(event){
            // 向父组件抛出一个事件
            this.$emit('changePage',event);
        }
    }
}
</script>

<style lang="less" scoped>
@import '~@/style/var.less';
.pager-container{
    display:flex;
    justify-content: center;
    margin:10px 0;
    a{
        margin:0 3px;
        cursor: pointer;
        color:@primary;
        &.disabled{
            color:@lightWords;
            cursor: not-allowed;
        }
        &.active{
            color:@words;
            cursor: text;
            font-weight:bold;
        }
    }
}
</style>