<template>
    <div class="project-container" v-loading="loading" ref="container">
        <!-- 循环条目 -->
        <div class="project-item" v-for="item in data" :key="item.id">
            <a :href="item.url || `javascript:alert('不能访问')`" :target="item.url? '_blank' : '_self'">
                <img v-lay="item.thumb" alt="" class="thumb">
            </a>
            <div class="info">
                <h2>
                    <a :href="item.url || `javascript:alert('不能访问')`" :target="item.url? '_blank' : '_self'">{{item.name}}</a>
                </h2>
                <a :href="item.github" target="_blank" class="github" v-if="item.github">github</a>
                <!-- 循环条目 -->
                <p v-for="(p,i) in item.description" :key="i">{{p}}</p>
            </div>
        </div>
    </div>
</template>
<style lang="less" scoped>
.project-container{
    width:100%;
    height:100%;
    overflow:auto;
    scroll-behavior: smooth;
    position:relative;
    padding:20px;
    box-sizing: border-box;
   
    .project-item{
        transition:.5s;
        padding:20px;
        display:flex;
        margin-bottom:20px;
         &:hover{
            box-shadow: -1px 1px 5px #000;
    transform: scale(1.01) translate(3px, -3px);
    color: inherit;
    }
        .thumb{
            width:250px;
            max-height:200px;
            flex:0 0 auto;
            object-fit: cover;
            margin-right:15px;
            border-radius: 5px;
        }
        .info{
            line-height:1.7;
            flex:1 1 auto;
            h2{
                margin:0;
            }
            .github{
                font-size:14px;
                color:#6b9eee;
            }
        }
    }
}
</style>
<script>
import {mapState} from 'vuex'
import loading from '@/directives/loading.js'
import {mainScroll} from '@/mixins/mainScroll.js'
export default {
    mixins:[mainScroll('container')],
    directives:{
        loading,
    },
    computed:{
        ...mapState('project',['loading','data']),
    },
    created() {
        this.$store.dispatch('project/fetchProject')
    },
}
</script>