<template>
    <ul class="rightList-container">
        <li v-for="(item,i) in list" :key="i" >
            <span @click="handleClick(item)" :class="{active:item.isSelected}">{{item.name}}</span>
             <span @click="handleClick(item)" v-if="item.articleCount>0" class="number" :class="{active:item.isSelected}">{{item.articleCount}}篇</span>
            <RightList :list="item.children" @itemClick="handleClick"></RightList>
        </li>
    </ul>
</template>

<script>

export default {
   
    // 组件递归
    name: 'RightList',
    props:{
        // [{name,isSelected,children:[{},{}]},{}]
        list:{
            type:Array,
            default:()=>[],
        }
    },
    methods:{
        handleClick(item){
            if(!item.isSelected){
                this.$emit('itemClick',item)
            }
            
        }
    }
 
    
};
</script>

<style lang="less" scoped>
@import '~@/style/var.less';
.rightList-container{
    margin-top:16px;
    .rightList-container{
        margin-left:1em;
        
    }
    li{
        font-size:14px;
        cursor: pointer;
        min-height:40px;
        line-height:40px;
         .number{
            font-size: 12px;
            margin-left: 1em;
            color: @gray;
        }
        .active{
            color:@warn;
            font-weight:bold;
        }
       
    }
    
}
</style>